import Vue from 'vue'
import Router from 'vue-router'
import Bocc from '@/pages/bocc'
import Meta from 'vue-meta'

Vue.use(Router)
Vue.use(Meta)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'bocc',
      component: Bocc
    }
  ]
})



